<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <div class="search-box">
                <!--  -->
                <el-input
                    placeholder=""
                    class="search-input"
                    v-model="queryInfo.kewords"
                    clearable
                    @clear="researchUser()"
                >
                    <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="searchUser()"
                    ></el-button>
                </el-input>
                <!--   -->
            </div>
            <!-- table -->
            <el-table :data="dataList">
                <!-- <el-table-column type="index" label="#"></el-table-column> -->
                <el-table-column
                    label="ID"
                    prop="id"
                    width="80px"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="city"
                    width="100px"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="type"
                    width="100px"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="content"
                    :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column label="">
                    <!--  -->
                    <template slot-scope="scope">
                        <!-- {{ scope.row.c_time | dateFormat }} -->
                        <div class="demo-image__preview">
                            <div
                                class="image-box"
                                v-for="(item, index) in scope.row.picarr"
                                :key="index"
                            >
                                <el-image
                                    :src="localhost + item.url"
                                    :preview-src-list="pictureArr"
                                    style="width: 60px; height: 60px"
                                >
                                </el-image>
                                <!-- <div
                                    class="image-delete"
                                    @click="deleteImage(index)"
                                >
                                    +
                                </div> -->
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="" width="160px">
                    <!--  -->
                    <template slot-scope="scope">
                        {{ scope.row.baoming ? scope.row.baoming.length : 0 }}
                    </template>
                </el-table-column>
                <el-table-column
                    label=""
                    prop="pname"
                    width="120px"
                    :show-overflow-tooltip="true"
                ></el-table-column>

                <el-table-column label="" width="160px">
                    <!--  -->
                    <template slot-scope="scope">
                        {{ scope.row.c_time | dateFormat }}
                    </template>
                </el-table-column>
                <el-table-column label="" width="180px">
                    <!--  -->
                    <template slot-scope="scope">
                        <!--   -->
                        <!-- <el-button
                            type="primary"
                            icon="el-icon-edit"
                            v-model="scope.row"
                            size="mini"
                            @click="showEditDialog(scope.row)"
                            ></el-button
                        > -->
                        <el-button
                            type="danger"
                            icon="el-icon-delete"
                            v-model="scope.row"
                            size="mini"
                            @click="removeById(scope.row.id)"
                            ></el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[20, 50, 100]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="this.total"
                background
            >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    // 
    props: ['userInfo'],
    data() {
        return {
            localhost: this.$http.defaults.baseURL.replace('/admin', ''),
            // 
            queryInfo: {
                kewords: '',
                pagenum: 1,
                pagesize: 20
            },
            dataList: [],
            total: 0,
            pictureArr: [] // 
        }
    },
    created() {
        this.getList()
    },
    methods: {
        // 
        formatePhotoArr() {
            this.pictureArr = [] // 
            this.editForm.photoarr.forEach((elem) => {
                this.pictureArr.push(this.localhost + elem.url)
            })
        },
        // 
        async getList() {
            let { data: res } = await this.$http.get('chat/getbclist', {
                params: this.queryInfo
            })
            //  div
            if (res.code !== 200) {
                this.total = 0
            } else {
                this.dataList = res.data
                // console.log(this.dataList)
                this.total = res.total
            }
        },
        // pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getUserList()
        },
        // pagenum
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getUserList()
        },

        //  
        searchUser() {
            this.queryInfo.pagenum = 1
            this.getList()
        },
        // 
        researchUser() {
            this.queryInfo.pagenum = 1
            this.getList()
        },

        // 
        async removeById(id) {
            let result = await this.$confirm(
                ', ?',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }
            ).catch((err) => err)
            if (result === 'confirm') {
                let { data: res } = await this.$http.delete('chat/deletebc', {
                    params: { id: id }
                })
                if (res.code === 200) {
                    this.getList() //  
                    this.$message.success({
                        message: '',
                        offset: 201,
                        center: true
                    })
                }
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
.el-card
    .search-box
        display flex
        justify-content space-between
        .search-input
            width: 400px;
        .add-button
            margin-right 17px
    .adduser-dialog
        .el-form
            padding 0px 100px 0px 85px
            .el-input
                width 400px
    .dialog-footer
        position absolute
        bottom 20px
        right 129px

.el-table
    margin-top 10px
.el-pagination
    margin-top 20px
    float right
.cart-sorry
    height 100px
    display flex
    align-items center
    justify-content center
    img
        width 100px
.el-select
    margin-left 150px
.image-box
    position relative
    float left
    margin-right 5px
    .image-delete
        font-size 20px
        transform rotate(-45deg)
        position absolute
        top -15px
        right 0px
        color red
        cursor pointer
    .image-top
        position absolute
        top -12px
        left 0
        i
            color green
            cursor pointer
</style>
