<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <div class="search-box">
                <div></div>
                <!--  -->
                <!-- <el-input
                    placeholder=""
                    class="search-input"
                    v-model="queryInfo.query"
                    clearable
                    @clear="researchUser()"
                >
                    <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="searchUser()"
                    ></el-button>
                </el-input> -->
                <!--   -->
                <el-button
                    type="primary"
                    class="add-button"
                    @click="addDialogVisible = true"
                    ></el-button
                >
            </div>
            <!-- table -->
            <el-table :data="dataList">
                <el-table-column type="index" label="#"></el-table-column>
                <!-- <el-table-column label="id" prop="id"></el-table-column> -->
                <el-table-column label="" prop="cate"></el-table-column>

                <el-table-column label="" width="180px">
                    <!--  -->
                    <template slot-scope="scope">
                        <!--   -->
                        <el-button
                            type="danger"
                            icon="el-icon-delete"
                            v-model="scope.row"
                            size="mini"
                            @click="removeUserById(scope.row.id)"
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-dialog
                class="adduser-dialog"
                :close-on-click-modal="false"
                title=""
                :visible.sync="addDialogVisible"
                width="700px"
                center
                @close="dialogClose"
            >
                <!--  -->
                <el-form
                    :model="addForm"
                    :rules="addFormRules"
                    ref="ruleFormRef"
                    label-width="100px"
                >
                    <el-form-item label="" prop="cate">
                        <el-input v-model="addForm.cate"></el-input>
                    </el-form-item>
                </el-form>
                <!--  -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="addUser"> </el-button>
                </span>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
export default {
    // 
    props: ['userInfo'],
    data() {
        return {
            addDialogVisible: false, // /
            dataList: [],
            // 
            addFormRules: {
                cate: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            addForm: {
                cate: ''
            }
        }
    },
    created() {
        this.getList()
    },
    methods: {
        // pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getUserInfo()
        },
        // pagenum
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getUserInfo()
        },
        dialogClose() {
            this.$refs.ruleFormRef.resetFields()
        },
        // 
        async getList() {
            const { data: res } = await this.$http.get('albums/getcates')
            if (res.code === 200) {
                this.dataList = res.data
            }
        },
        //  
        addUser() {
            this.$refs.ruleFormRef.validate(async (valid) => {
                if (valid) {
                    // this.addDialogVisible = false
                    let { data: res } = await this.$http.post(
                        'albums/addcate',
                        this.addForm
                    )
                    if (res.code === 200) {
                        this.getList() //  
                        this.$message.success({
                            message: '',
                            offset: 201,
                            center: true
                        })
                        // 
                        this.addDialogVisible = false
                    } else {
                        this.$message.error({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    }
                }
            })
        },

        // 
        async removeUserById(id) {
            let result = await this.$confirm(
                ', ?',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }
            ).catch((err) => err)
            if (result === 'confirm') {
                let { data: res } = await this.$http.delete('albums/delcate', {
                    params: { id: id }
                })
                if (res.code === 200) {
                    this.getList() //  
                    this.$message.success({
                        message: '',
                        offset: 201,
                        center: true
                    })
                }
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
.el-card
    .search-box
        display flex
        justify-content space-between
        .search-input
            width: 400px;
        .add-button
            margin-right 17px
    .adduser-dialog
        .el-form
            padding 0px 100px 0px 85px
            .el-input
                width 400px
    .dialog-footer
        position absolute
        bottom 20px
        right 129px

.el-table
    margin-top 10px
.el-pagination
    margin-top 20px
    float right
.cart-sorry
    height 100px
    display flex
    align-items center
    justify-content center
    img
        width 100px
.el-select
    margin-left 150px
</style>
