<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <div class="search-box">
                <!--  -->
                <el-input
                    placeholder=""
                    class="search-input"
                    v-model="queryInfo.keywords"
                    clearable
                    @clear="researchUser()"
                >
                    <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="searchUser()"
                    ></el-button>
                </el-input>
                <!--   -->
                <!-- <el-button
                    type="primary"
                    class="add-button"
                    @click="addDialogVisible = true"
                    ></el-button
                > -->
                <!--  -->
                <!-- <span class="span">* </span> -->
                <el-cascader
                    class="selector"
                    ref="elcascader"
                    clearable
                    v-model="idArr"
                    placeholder="/"
                    :options="citiesList"
                    :props="citiesprops"
                    @change="handleChange"
                    @visible-change="elCascaderOnlick"
                    @expand-change="elCascaderOnlick"
                >
                </el-cascader>

                <el-select
                    class="selector"
                    v-model="typeValue"
                    placeholder=""
                    clearable
                    @change="typeChange"
                >
                    <el-option
                        v-for="item in typeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    >
                    </el-option>
                </el-select>
                <el-select
                    class="selector"
                    v-model="rzValue"
                    placeholder=""
                    clearable
                    @change="rzChange"
                >
                    <el-option
                        v-for="item in rzOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    >
                    </el-option>
                </el-select>
                <el-select
                    class="selector"
                    v-model="tjValue"
                    placeholder=""
                    clearable
                    @change="tjChange"
                >
                    <el-option
                        v-for="item in tjOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    >
                    </el-option>
                </el-select>
                <el-select
                    class="selector"
                    v-model="workValue"
                    placeholder=""
                    clearable
                    @change="workChange"
                >
                    <el-option
                        v-for="item in workOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    >
                    </el-option>
                </el-select>
            </div>
            <!-- table -->
            <el-table :data="userlist">
                <!-- <el-table-column type="index" label="#"></el-table-column> -->
                <el-table-column label="" width="50">
                    <!--  -->
                    <template slot-scope="scope">
                        <img
                            :src="localhost + scope.row.cover[0].url"
                            alt=""
                            style="
                                height: 40px;
                                width: 40px;
                                border-radius: 50%;
                                margin-top: 5px;
                            "
                        />
                    </template>
                </el-table-column>
                <el-table-column
                    label=""
                    prop="username"
                    :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="city"
                    width="80px"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="address"
                    :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="type"
                    :show-overflow-tooltip="true"
                    width="80"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="price"
                    width="80"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="remarks"
                    :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column label="" prop="contact"></el-table-column>
                <el-table-column label="" width="80">
                    <!--  -->
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.rengzheng"
                            @change="renzhengChange(scope.row)"
                        ></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-input
                            v-model="scope.row.pname"
                            @blur="editPname(scope.row)"
                        ></el-input>
                    </template>
                </el-table-column>
                <el-table-column label="" width="80">
                    <!--  -->
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.totop"
                            @change="topChange(scope.row)"
                        ></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="80">
                    <!--  -->
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            @change="statusChange(scope.row)"
                        ></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" :width="tableWidth">
                    <template slot-scope="scope">
                        <div
                            class=""
                            @click="remarkClick(scope.row)"
                            v-if="scope.row.id"
                            style="
                                min-width: 100px;
                                min-height: 50px;
                                cursor: pointer;
                            "
                        >
                            {{ scope.row.admin_remarks }}
                        </div>
                        <el-input
                            ref="remarkInput"
                            v-if="remark_id == scope.row.id"
                            v-model="scope.row.admin_remarks"
                            @blur="editRemarks(scope.row)"
                        ></el-input>
                    </template>
                </el-table-column>

                <el-table-column label="" width="120px">
                    <!--  -->
                    <template slot-scope="scope">
                        <!--   -->
                        <el-button
                            type="primary"
                            icon="el-icon-edit"
                            v-model="scope.row"
                            size="mini"
                            @click="showEditDialog(scope.row)"
                        ></el-button>
                        <el-button
                            type="danger"
                            icon="el-icon-delete"
                            v-model="scope.row"
                            size="mini"
                            @click="removeUserById(scope.row.id)"
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-dialog
                class="adduser-dialog"
                :close-on-click-modal="false"
                title=""
                :visible.sync="editDialogVisible"
                width="700px"
                center
                @close="dialogClose"
            >
                <!--  -->
                <el-form :model="editForm" ref="ruleFormRef" label-width="80px">
                    <el-form-item label="ID" prop="id">
                        <el-input v-model="editForm.id" disabled=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="username">
                        <el-input v-model="editForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="age">
                        <el-input v-model="editForm.age"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="height">
                        <el-input v-model="editForm.height"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="weight">
                        <el-input v-model="editForm.weight"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="bust">
                        <el-input v-model="editForm.bust"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="address">
                        <el-input v-model="editForm.address"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="price">
                        <el-input v-model="editForm.price"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="price_remarks">
                        <el-input v-model="editForm.price_remarks"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="big_fan">
                        <el-input v-model="editForm.big_fan"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="contact">
                        <el-input v-model="editForm.contact"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="remarks">
                        <el-input v-model="editForm.remarks"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <div class="demo-image__preview">
                            <div
                                class="image-box"
                                v-for="(item, index) in editForm.photoarr"
                                :key="index"
                            >
                                <el-image
                                    :src="localhost + item.url"
                                    :preview-src-list="pictureArr"
                                    style="width: 60px; height: 60px"
                                >
                                </el-image>
                                <div
                                    class="image-delete"
                                    @click="deleteImage(index)"
                                >
                                    +
                                </div>
                            </div>
                        </div>
                    </el-form-item>
                    <el-form-item label="" prop="videoarr">
                        <div class="demo-image__preview">
                            <div
                                class="image-box"
                                v-for="(item, index) in editForm.videoarr"
                                :key="index"
                            >
                                <video
                                    controls
                                    :src="localhost + item.url"
                                    style="width: 150px; height: 120px"
                                ></video>
                                <div
                                    class="image-delete"
                                    @click="deleteVideo(index)"
                                >
                                    +
                                </div>
                                <div
                                    class="image-top"
                                    v-if="index > 0"
                                    @click="setTop(item, index)"
                                >
                                    <i
                                        class="iconfont iconxuanxiangshangyi"
                                    ></i>
                                </div>
                            </div>
                        </div>
                    </el-form-item>
                </el-form>
                <!--  -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="editDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="updateUser"
                        > </el-button
                    >
                </span>
            </el-dialog>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[20, 50, 100]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="this.total"
                background
            >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    // 
    props: ['userInfo'],
    data() {
        return {
            localhost: this.$http.defaults.baseURL.replace('/admin', ''),
            // 
            queryInfo: {
                keywords: '',
                pagenum: 1,
                pagesize: 20,
                city: '',
                area: '',
                type: '',
                rengzheng: '',
                totop: '',
                work: ''
            },
            userlist: [],
            total: 0,
            addDialogVisible: false, // /
            editDialogVisible: false, // 
            // 
            addFormRules: {
                username: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 4,
                        max: 10,
                        message: ' 4  10 ',
                        trigger: 'blur'
                    }
                ],
                password: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 8,
                        max: 20,
                        message: ' 8  20 ',
                        trigger: 'blur'
                    }
                ],
                gaipassword: [
                    {
                        min: 8,
                        max: 20,
                        message: ' 8  20 ',
                        trigger: 'blur'
                    }
                ],
                mobile: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ],
                email: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            addForm: {
                username: '',
                password: '',
                mobile: '',
                email: '',
                id: '',
                user_pid: this.userInfo.username,
                user_level: 3
            },
            // 
            editForm: {
                username: '',
                age: '',
                height: '',
                weight: '',
                bust: '',
                address: '',
                price: '',
                price_remarks: '',
                big_fan: '',
                contact: '',
                remarks: '',
                photoarr: '',
                videoarr: ''
            },
            pictureArr: [], // 
            idArr: [],
            citiesList: [],
            // 
            citiesprops: {
                checkStrictly: true,
                expandTrigger: 'hover',
                value: 'id',
                label: 'city',
                children: 'children'
            },
            typeValue: '',
            typeOptions: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: '',
                    label: ''
                }
            ],
            rzValue: '',
            rzOptions: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: '',
                    label: ''
                }
            ],
            tjValue: '',
            tjOptions: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: '',
                    label: ''
                }
            ],
            workValue: '',
            workOptions: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: '',
                    label: ''
                }
            ],
            remark_id: '',
            tableWidth: ''
        }
    },
    created() {
        this.getList()
        this.getCitiesList()
    },
    methods: {
        // 
        async getList() {
            let { data: res } = await this.$http.get('albums/getalbumst', {
                params: this.queryInfo
            })
            //  div
            if (res.code === 200) {
                this.userlist = res.data
                this.total = res.total
            } else {
                this.total = 0
            }
        },
        //  
        async getCitiesList() {
            const { data: res } = await this.$http.get('city/getcities', {
                params: {
                    pagesize: 1000,
                    pagenum: 1,
                    type: 2
                }
            })
            if (res.code === 200) {
                this.citiesList = res.data
            }
        },
        // pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getList()
        },
        // pagenum
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getList()
        },
        // 
        async renzhengChange(item) {
            let result = await this.$http.put('albums/renzheng', {
                id: item.id,
                rengzheng: item.rengzheng
            })
            if (result) this.getList()
        },
        // 
        async topChange(item) {
            let result = await this.$http.put('albums/totop', {
                id: item.id,
                totop: item.totop
            })
            if (result) this.getList()
        },
        // 
        async statusChange(item) {
            let result = await this.$http.put('albums/upstatus', {
                id: item.id,
                status: item.status
            })
            if (result) this.getList()
        },

        //  
        searchUser() {
            this.queryInfo.pagenum = 1
            this.getList()
        },
        // 
        researchUser() {
            this.queryInfo.pagenum = 1
            this.getList()
        },
        //   ref="ruleFormRef"
        dialogClose() {
            this.$refs.ruleFormRef.resetFields()
        },

        // 
        showEditDialog(item) {
            this.editDialogVisible = true
            this.editForm = JSON.parse(JSON.stringify(item))
            this.formatePhotoArr()
        },
        // 
        formatePhotoArr() {
            this.pictureArr = [] // 
            this.editForm.photoarr.forEach((elem) => {
                this.pictureArr.push(this.localhost + elem.url)
            })
        },
        // pic
        deleteImage(e) {
            this.editForm.photoarr.splice(e, 1)
            this.formatePhotoArr()
        },
        deleteVideo(e) {
            this.editForm.videoarr.splice(e, 1)
        },
        // 
        setTop(elem, index) {
            this.editForm.videoarr.unshift(elem)
            this.editForm.videoarr.splice(index + 1, 1)
        },
        // 
        updateUser() {
            this.$refs.ruleFormRef.validate(async (valid) => {
                if (valid) {
                    let { data: res } = await this.$http.put(
                        'albums/updategirl',
                        this.editForm
                    )
                    if (res.code === 200) {
                        this.editDialogVisible = false // 
                        this.getList() //  
                        this.$message.success({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    } else {
                        this.$message.error({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    }
                }
            })
        },
        // 
        async removeUserById(id) {
            let result = await this.$confirm(
                ', ?',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }
            ).catch((err) => err)
            if (result === 'confirm') {
                let { data: res } = await this.$http.delete('albums/delgirl', {
                    params: { id: id }
                })
                if (res.code === 200) {
                    this.getList() //  
                    this.$message.success({
                        message: '',
                        offset: 201,
                        center: true
                    })
                }
            }
        },
        // 
        async editPname(row) {
            const { data: res } = await this.$http.put('albums/uppname', {
                id: row.id,
                pname: row.pname
            })
            if (res.code === 200) {
                this.$message.success({
                    message: '',
                    offset: 201,
                    center: true
                })
            } else if (res.code === 204) {
                this.$message.error({
                    message: ',',
                    offset: 201,
                    center: true
                })
            } else {
                this.$message.error({
                    message: ', ',
                    offset: 201,
                    center: true
                })
            }
        },
        // 
        handleChange(val) {
            // if (this.idArr.length !== 3) {
            //     this.idArr = []
            //     this.goodsForm.goods_gameid = 0
            //     this.goodsForm.goods_quid = 0
            //     this.goodsForm.goods_catname = ''
            //     this.canshuData = []
            // } else {
            //     this.goodsForm.goods_gameid = this.idArr[0] // id 
            //     this.goodsForm.goods_quid = this.idArr[1] // id 
            //     let checkedName =
            //         this.$refs['elcascader'].getCheckedNodes()[0].pathLabels
            //     let checKedNameStr = checkedName.join(' / ')
            //     this.goodsForm.goods_catname = checKedNameStr //  
            //     // 
            //     this.tabClicked()
            // }
            if (this.$refs['elcascader'].getCheckedNodes().length > 0) {
                let checkedName =
                    this.$refs['elcascader'].getCheckedNodes()[0].pathLabels

                // let checKedNameStr = checkedName.join(' / ')
                this.queryInfo.city = ''
                this.queryInfo.area = ''
                this.queryInfo.city = checkedName[0]
                if (checkedName.length >= 2) {
                    this.queryInfo.area = checkedName[1]
                }
            } else {
                this.queryInfo.city = ''
                this.queryInfo.area = ''
            }

            this.getList()
        },
        // bug
        elCascaderOnlick() {
            let that = this
            setTimeout(function () {
                document
                    .querySelectorAll('.el-cascader-node__label')
                    .forEach((el) => {
                        el.onclick = function () {
                            this.previousElementSibling.click()
                            that.$refs.elcascader.dropDownVisible = false
                        }
                    })
                document
                    .querySelectorAll('.el-cascader-panel .el-radio')
                    .forEach((el) => {
                        el.onclick = function () {
                            that.$refs.elcascader.dropDownVisible = false
                        }
                    })
            }, 100)
        },

        typeChange(e) {
            this.queryInfo.type = e
            this.getList()
        },
        rzChange(e) {
            this.queryInfo.rengzheng = e
            this.getList()
        },
        tjChange(e) {
            this.queryInfo.totop = e
            this.getList()
        },
        workChange(e) {
            this.queryInfo.work = e
            this.getList()
        },
        // /
        async editRemarks(row) {
            const { data: res } = await this.$http.put('albums/upremarks', {
                id: row.id,
                admin_remarks: row.admin_remarks
            })
            if (res.code === 200) {
                this.remark_id = ''
                this.tableWidth = ''
            } else {
                this.$message.error({
                    message: ', ',
                    offset: 201,
                    center: true
                })
            }
        },
        remarkClick(row) {
            this.remark_id = row.id
            this.tableWidth = 400
            this.$nextTick(() => {
                this.$refs.remarkInput.focus()
            })
        }
    }
}
</script>

<style lang="stylus" scoped>
.el-card
    .search-box
        display block
        .search-input
            width: 400px;
        .add-button
            margin-right 17px
        .selector
            margin-right 0
            margin-left 10px
            width 150px
    .adduser-dialog
        .el-form
            padding 0px 30px 0px 35px
            .el-input
                width 400px
    .dialog-footer
        position absolute
        bottom 20px
        right 129px

.el-table
    margin-top 10px
.el-pagination
    margin-top 20px
    float right
.cart-sorry
    height 100px
    display flex
    align-items center
    justify-content center
    img
        width 100px
.el-select
    margin-left 150px
.image-box
    position relative
    float left
    margin-right 5px
    .image-delete
        font-size 20px
        transform rotate(-45deg)
        position absolute
        top -15px
        right 0px
        color red
        cursor pointer
    .image-top
        position absolute
        top -12px
        left 0
        i
            color green
            cursor pointer
</style>
