<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <div class="search-box">
                <div></div>
                <!--  -->
                <!-- <el-input
                    placeholder=""
                    class="search-input"
                    v-model="queryInfo.query"
                    clearable
                    @clear="researchUser()"
                >
                    <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="searchUser()"
                    ></el-button>
                </el-input> -->
                <!--   -->
                <el-button
                    type="primary"
                    class="add-button"
                    @click="addDialogVisible = true"
                    ></el-button
                >
            </div>
            <!-- table -->
            <el-table :data="userlist" v-if="flag">
                <!-- <el-table-column type="index" label="#"></el-table-column> -->
                <el-table-column label="" prop="username"></el-table-column>
                <el-table-column label="" prop="username"></el-table-column>
                <el-table-column label="" prop="user_pid"></el-table-column>
                <el-table-column label="" prop="mobile"></el-table-column>
                <el-table-column label="" prop="email"></el-table-column>
                <el-table-column label="" prop="email"></el-table-column>
                <el-table-column label="" prop="email"></el-table-column>

                <el-table-column label="">
                    <!--  -->
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.user_state"
                            @change="userStateChange(scope.row)"
                        ></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="180px">
                    <!--  -->
                    <template slot-scope="scope">
                        <!--   -->
                        <el-button
                            type="primary"
                            icon="el-icon-edit"
                            v-model="scope.row"
                            size="mini"
                            @click="showEditDialog(scope.row.id)"
                        ></el-button>
                        <el-button
                            type="danger"
                            icon="el-icon-delete"
                            v-model="scope.row"
                            size="mini"
                            @click="removeUserById(scope.row.id)"
                        ></el-button>
                        <el-tooltip
                            class="item"
                            effect="light"
                            content=""
                            placement="top"
                            :enterable="false"
                        >
                            <el-button
                                type="warning"
                                icon="el-icon-setting"
                                size="mini"
                                @click="setRights(scope.row.id)"
                            ></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <!-- <div class="cart-sorry" v-if="!flag">
                <img src="../../assets/images/sorry.png" alt="">
                <span></span>
            </div> -->
            <!--  -->
            <el-dialog
                class="setRights-dialog"
                :close-on-click-modal="false"
                title=""
                :visible.sync="setRoleDialogVisible"
                width="700px"
                center
                @close="roleDialogClose"
            >
                <el-select
                    v-model="roleInfo.user_level"
                    placeholder=""
                >
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    >
                    </el-option>
                </el-select>
                <!--  -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="setRoleDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="setRole"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <el-dialog
                class="adduser-dialog"
                :close-on-click-modal="false"
                title=""
                :visible.sync="addDialogVisible"
                width="700px"
                center
                @close="dialogClose"
            >
                <!--  -->
                <el-form
                    :model="addForm"
                    :rules="addFormRules"
                    ref="ruleFormRef"
                    label-width="60px"
                >
                    <el-form-item label="" prop="username">
                        <el-input
                            v-model="addForm.username"
                            @blur="checkName(addForm)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="password">
                        <el-input v-model="addForm.password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="mobile">
                        <el-input
                            v-model="addForm.mobile"
                            @blur="checkMobile(addForm)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="email">
                        <el-input
                            v-model="addForm.email"
                            @blur="checkEmail(addForm)"
                        ></el-input>
                    </el-form-item>
                </el-form>
                <!--  -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="addUser"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <el-dialog
                class="adduser-dialog"
                :close-on-click-modal="false"
                title=""
                :visible.sync="editDialogVisible"
                width="700px"
                center
                @close="dialogClose"
            >
                <!--  -->
                <el-form
                    :model="editForm"
                    :rules="addFormRules"
                    ref="ruleFormRef"
                    label-width="66px"
                >
                    <el-form-item label="" prop="username">
                        <el-input
                            v-model="editForm.username"
                            disabled=""
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="mobile">
                        <el-input
                            v-model="editForm.mobile"
                            @blur="checkMobile(editForm)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="email">
                        <el-input
                            v-model="editForm.email"
                            @blur="checkEmail(editForm)"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="gaipassword">
                        <el-input
                            v-model="editForm.gaipassword"
                            type="password"
                        ></el-input>
                    </el-form-item>
                </el-form>
                <!--  -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="editDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="updateUser"
                        > </el-button
                    >
                </span>
            </el-dialog>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[20, 50, 100]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="this.total"
                background
            >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    // 
    props: ['userInfo'],
    data() {
        // 
        let checkEmail = (rule, value, cb) => {
            const regEmail =
                /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
            if (regEmail.test(value)) {
                return cb()
            }
            cb(new Error(''))
        }
        // 
        let checkMobile = (rule, value, cb) => {
            const regMobile =
                /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/
            if (regMobile.test(value)) {
                return cb()
            }
            cb(new Error(''))
        }
        // 
        let isName = (rule, value, cb) => {
            if (this.isname === '') {
                return cb()
            }
            cb(new Error(''))
        }
        // 
        let isEmail = (rule, value, cb) => {
            if (this.isemail === '') {
                return cb()
            }
            cb(new Error(''))
        }
        // 
        let isMobile = (rule, value, cb) => {
            if (this.ismobile === '') {
                return cb()
            }
            cb(new Error(''))
        }

        return {
            // 
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 20
            },
            userlist: [],
            total: 3,
            flag: true, // / /
            addDialogVisible: false, // /
            editDialogVisible: false, // 
            isname: '', // rule
            isemail: '',
            ismobile: '',
            // 
            addFormRules: {
                username: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 4,
                        max: 10,
                        message: ' 4  10 ',
                        trigger: 'blur'
                    },
                    {
                        validator: isName,
                        trigger: 'blur'
                    }
                ],
                password: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 8,
                        max: 20,
                        message: ' 8  20 ',
                        trigger: 'blur'
                    }
                ],
                gaipassword: [
                    {
                        min: 8,
                        max: 20,
                        message: ' 8  20 ',
                        trigger: 'blur'
                    }
                ],
                mobile: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: checkMobile,
                        trigger: 'blur'
                    },
                    {
                        validator: isMobile,
                        trigger: 'blur'
                    }
                ],
                email: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: checkEmail,
                        trigger: 'blur'
                    },
                    {
                        validator: isEmail,
                        trigger: 'blur'
                    }
                ]
            },
            // 
            addForm: {
                username: '',
                password: '',
                mobile: '',
                email: '',
                id: '',
                user_pid: this.userInfo.username,
                user_level: 3
            },
            // 
            editForm: {
                username: '',
                mobile: '',
                email: '',
                id: '',
                gaipassword: ''
            },
            // 
            setRoleDialogVisible: false,
            // 
            options: [
                {
                    value: '1',
                    label: ''
                },
                {
                    value: '2',
                    label: ''
                },
                {
                    value: '3',
                    label: ''
                }
            ],
            // value
            roleInfo: {
                user_level: '',
                id: ''
            }
        }
    },
    created() {
        this.getUserInfo()
    },
    methods: {
        // 
        async getUserInfo() {
            let { data: res } = await this.$http.get('users/userlist', {
                params: this.queryInfo
            })
            //  div
            if (res.meta.status !== 200) {
                this.flag = false
                this.total = 0
            } else {
                this.userlist = res.data
                this.total = res.total
                this.flag = true
            }
        },
        // pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getUserInfo()
        },
        // pagenum
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getUserInfo()
        },
        // 
        async userStateChange(userinfo) {
            let result = await this.$http.put('users/updatestate', {
                username: userinfo.username,
                user_state: userinfo.user_state
            })
            if (result) this.getUserInfo()
        },
        //  
        searchUser() {
            this.queryInfo.pagenum = 1
            this.getUserInfo()
        },
        // 
        researchUser() {
            this.queryInfo.pagenum = 1
            this.getUserInfo()
        },
        //   ref="ruleFormRef"
        dialogClose() {
            this.$refs.ruleFormRef.resetFields()
        },
        //  
        addUser() {
            this.$refs.ruleFormRef.validate(async (valid) => {
                if (valid) {
                    // this.addDialogVisible = false
                    let { data: res } = await this.$http.post(
                        'users/adduser',
                        this.addForm
                    )
                    if (res.meta.status === 201) {
                        this.getUserInfo() //  
                        this.$message.success({
                            message: '',
                            offset: 201,
                            center: true
                        })
                        // 
                        this.addDialogVisible = false
                    } else {
                        this.$message.error({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    }
                }
            })
        },
        //     ( formname)
        checkName(formname) {
            // isname
            this.isname = ''
            // api
            this.$refs.ruleFormRef.validateField(
                'username',
                async (errorMessage) => {
                    if (!errorMessage) {
                        let { data: res } = await this.$http.get(
                            'users/addcheck',
                            { params: { username: formname.username } }
                        )
                        if (res === 1) {
                            // isname
                            this.isname = 'exist'
                            // 
                            this.$refs.ruleFormRef.validateField('username')
                        }
                    }
                }
            )
        },
        // 
        checkMobile(formname) {
            // ismobile
            this.ismobile = ''
            // api
            this.$refs.ruleFormRef.validateField(
                'mobile',
                async (errorMessage) => {
                    if (!errorMessage) {
                        let { data: res } = await this.$http.get(
                            'users/addcheck',
                            {
                                params: {
                                    mobile: formname.mobile,
                                    id: formname.id
                                }
                            }
                        )
                        if (res === 1) {
                            // ismobile
                            this.ismobile = 'exist'
                            // 
                            this.$refs.ruleFormRef.validateField('mobile')
                        }
                    }
                }
            )
        },
        // 
        checkEmail(formname) {
            // isemail
            this.isemail = ''
            // api
            this.$refs.ruleFormRef.validateField(
                'email',
                async (errorMessage) => {
                    if (!errorMessage) {
                        let { data: res } = await this.$http.get(
                            'users/addcheck',
                            {
                                params: {
                                    email: formname.email,
                                    id: formname.id
                                }
                            }
                        )
                        if (res === 1) {
                            // isemail
                            this.isemail = 'exist'
                            // 
                            this.$refs.ruleFormRef.validateField('email')
                        }
                    }
                }
            )
        },
        // 
        async showEditDialog(id) {
            this.editDialogVisible = true
            let { data: res } = await this.$http.get('users/getbyid', {
                params: { id: id }
            })
            this.editForm = res.data
            this.editForm.id = id
        },
        // 
        updateUser() {
            this.$refs.ruleFormRef.validate(async (valid) => {
                if (valid) {
                    let { data: res } = await this.$http.put(
                        'users/updateuser',
                        this.editForm
                    )
                    if (res.meta.status === 201) {
                        this.editDialogVisible = false // 
                        this.getUserInfo() //  
                        this.$message.success({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    } else {
                        this.$message.error({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    }
                }
            })
        },
        // 
        async removeUserById(id) {
            let result = await this.$confirm(
                ', ?',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }
            ).catch((err) => err)
            if (result === 'confirm') {
                let { data: res } = await this.$http.delete(
                    'users/deleteuser',
                    { params: { id: id } }
                )
                if (res.meta.status === 201) {
                    this.getUserInfo() //  
                    this.$message.success({
                        message: '',
                        offset: 201,
                        center: true
                    })
                }
            }
        },
        // 
        setRights(id) {
            this.setRoleDialogVisible = true
            this.roleInfo.id = id
        },
        // 
        roleDialogClose() {
            this.roleInfo = {}
        },
        // 
        async setRole() {
            let { data: res } = await this.$http.put(
                'users/setrole',
                this.roleInfo
            )
            if (res.status === 200) {
                this.setRoleDialogVisible = false
                this.getUserInfo()
                this.$message.success({
                    message: '',
                    offset: 201,
                    center: true
                })
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
.el-card
    .search-box
        display flex
        justify-content space-between
        .search-input
            width: 400px;
        .add-button
            margin-right 17px
    .adduser-dialog
        .el-form
            padding 0px 100px 0px 85px
            .el-input
                width 400px
    .dialog-footer
        position absolute
        bottom 20px
        right 129px

.el-table
    margin-top 10px
.el-pagination
    margin-top 20px
    float right
.cart-sorry
    height 100px
    display flex
    align-items center
    justify-content center
    img
        width 100px
.el-select
    margin-left 150px
</style>
