<template>
    <div id="setinfo">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-form
                :model="settingForm"
                :rules="settingFormRules"
                ref="settingFormRef"
                label-width="150px"
            >
                <!-- <el-form-item label="" prop="linkname">
                    <el-input
                        v-model="settingForm.linkname"
                        placeholder=""
                    ></el-input>
                </el-form-item> -->
                <!-- <el-form-item label="" prop="nodepname">
                    <el-select
                        v-model="selecValue"
                        placeholder=""
                        @change="selectChange"
                        clearable
                    >
                        <el-option
                            v-for="item in pidOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        >
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <!-- <el-form-item label="" prop="linkurl">
                    <el-input
                        v-model="settingForm.linkurl"
                        placeholder=""
                    ></el-input>
                </el-form-item> -->
                <!-- <el-form-item label="" prop="title">
                    <el-input
                        v-model="settingForm.title"
                        placeholder=""
                    ></el-input>
                </el-form-item> -->
                <!-- <el-form-item label="" prop="keywords">
                    <el-input
                        v-model="settingForm.keywords"
                        placeholder=""
                    ></el-input>
                </el-form-item> -->
                <el-form-item label="" prop="album_code">
                    <el-input
                        type="number"
                        v-model="settingForm.album_code"
                        placeholder=""
                    ></el-input>
                </el-form-item>
                <el-form-item label="" prop="qq">
                    <el-input
                        type="number"
                        v-model="settingForm.qq"
                        placeholder="ID6"
                    ></el-input>
                </el-form-item>
                <el-form-item label="" prop="domain">
                    <el-input
                        type="text"
                        v-model="settingForm.domain"
                        placeholder=" jy123456.shop"
                    ></el-input>
                </el-form-item>
                <el-form-item label="" prop="tgurl">
                    <el-input
                        type="text"
                        v-model="settingForm.tgurl"
                        placeholder=" https://t.me/+yC6OqGf9vRthN2I1"
                    ></el-input>
                </el-form-item>
                <el-form-item label="APP" prop="appdown">
                    <el-input
                        type="text"
                        v-model="settingForm.appdown"
                        placeholder="APP"
                    ></el-input>
                </el-form-item>
                <!-- <el-form-item label="" prop="email">
                    <el-input
                        type="number"
                        v-model="settingForm.email"
                        placeholder="ID6"
                    ></el-input>
                </el-form-item> -->
                <el-form-item label="" prop="notice">
                    <el-input
                        style="width: 600px"
                        type="textarea"
                        :rows="5"
                        v-model="settingForm.notice"
                        placeholder=""
                    ></el-input>
                </el-form-item>
                <el-form-item label="" prop="notice">
                    <el-switch v-model="settingForm.isreg"> </el-switch>
                </el-form-item>

                <el-form-item prop="notice" v-if="!settingForm.isreg">
                    <el-input
                        style="width: 600px"
                        type="textarea"
                        :rows="5"
                        v-model="settingForm.email"
                        placeholder=""
                    ></el-input>
                </el-form-item>

                <br />
                <el-button type="primary" @click="addBtn" class="editBtn"
                    ></el-button
                >
            </el-form>
        </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
            selecValue: '',
            // 
            settingForm: {
                album_code: '',
                notice: '',
                qq: '',
                email: '',
                domain: '',
                isreg: false,
                appdown: '',
                tgurl: ''
            },
            // 
            settingFormRules: {
                // linkname: [
                //     {
                //         required: true,
                //         message: '',
                //         trigger: 'blur'
                //     }
                // ],

                album_code: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            pidOptions: []
        }
    },
    //  
    created() {
        // 
        this.getLink()
    },
    // 
    methods: {
        // 
        addBtn() {
            this.$refs.settingFormRef.validate(async (valid) => {
                if (!valid) {
                    return this.$message.error({
                        message: '',
                        offset: 350,
                        center: true
                    })
                }
                const { data: res } = await this.$http.put(
                    'picture/upsett',
                    this.settingForm
                )
                if (res.code === 200) {
                    // 
                    return this.$message.success({
                        message: '',
                        offset: 350
                    })
                } else if (res.code === 204) {
                    return this.$message.error({
                        message: '',
                        offset: 350,
                        center: true
                    })
                }
            })
        },
        // 
        async getLink() {
            const { data: res } = await this.$http.get('picture/getsett')
            if (res.code === 200) {
                this.settingForm = res.data
            }
        }
        // select 
        // selectChange(e) {
        //     // pid
        //     this.settingForm.nodepid = e
        //     this.pidOptions.find((elem) => {
        //         if (elem.value === e) {
        //             // pname
        //             // console.log(elem)
        //             this.settingForm.nodepname = elem.label
        //         }
        //         // e 
        //         if (!e) {
        //             this.settingForm.nodepname = ''
        //         }
        //     })
        // }
    }
}
</script>

<style lang="stylus" scoped>
#setinfo
    .el-card
        width 1050px
    .el-form
        margin-left 100px
    .el-input
        width 600px
    .texta
        width 600px
    .logo
        width 250px
        height: 250px
    .editBtn
        margin-left 30px
        width 720px
        margin-top 30px
</style>
