/* eslint-disable vue/valid-template-root */ /* eslint-disable
vue/valid-template-root */ /* eslint-disable vue/valid-template-root */
<template>
    <div id="catelist">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-button
                type="primary"
                style="float: right; margin-right: 17px; margin-bottom: 20px"
                @click="getCate"
                >/</el-button
            >
            <!--  -->
            <tree-table
                border
                :data="catelist"
                :columns="columns"
                :selection-type="false"
                :expand-type="false"
            >
                <!--  -->
                <template slot="order" slot-scope="scope">
                    <!-- order  -->
                    <el-row v-if="scope.row.pid == 0">
                        <el-input
                            ref="orderInput"
                            v-model="scope.row.order"
                            @blur="
                                changeOrder(
                                    scope.row.id,
                                    scope.row.pid,
                                    scope.row.order
                                )
                            "
                        ></el-input>
                    </el-row>
                    <!-- <el-row v-if="scope.row.order == 0">
                        <el-input
                            ref="orderInput"
                            v-model="scope.row.children_order"
                            @blur="children_order(scope.row)"
                        ></el-input>
                    </el-row> -->
                </template>
                <template slot="children_order" slot-scope="scope">
                    <!-- order  -->
                    <!-- <el-row v-if="scope.row.pid !== 0">
                        <el-input
                            ref="orderInput"
                            v-model="scope.row.order"
                            @blur="children_order(scope.row)"
                        ></el-input>
                    </el-row> -->
                    <el-row v-if="scope.row.order == 0">
                        <el-input
                            ref="orderInput"
                            v-model="scope.row.children_order"
                            @blur="
                                changeOrder(
                                    scope.row.id,
                                    scope.row.pid,
                                    scope.row.children_order
                                )
                            "
                        ></el-input>
                    </el-row>
                </template>

                <!--  -->
                <template slot="city_model" slot-scope="scope">
                    <el-row v-if="scope.row.order !== 0">
                        &nbsp;&nbsp;
                        <el-switch
                            v-model="scope.row.city_model"
                            @change="cityModelChange(scope.row)"
                        ></el-switch>
                        &nbsp;&nbsp;
                    </el-row>
                </template>
                <!--  -->
                <template slot="operate" slot-scope="scope">
                    <el-row>
                        <el-button
                            size="mini"
                            type="primary"
                            icon="el-icon-edit"
                            @click="cateEdit(scope.row)"
                            ></el-button
                        >
                        <el-button
                            size="mini"
                            type="danger"
                            icon="el-icon-delete"
                            @click="cateDelete(scope.row)"
                            ></el-button
                        >
                    </el-row>
                </template>
                <!--  -->
            </tree-table>
        </el-card>
        <!-- / -->
        <el-dialog
            title="/"
            :close-on-click-modal="false"
            :visible.sync="dialogVisible"
            width="700px"
            center
            @close="dialogClose"
        >
            <!--  -->
            <el-form
                ref="cateFormRef"
                :model="cateForm"
                :rules="cateFormRules"
                label-width="100px"
            >
                <!--  -->
                <el-form-item label="/" prop="city">
                    <el-input
                        v-model="cateForm.city"
                        class="cate_name"
                        placeholder=""
                    ></el-input>
                </el-form-item>
                <!--   -->
                <el-form-item label="">
                    <div class="block">
                        <!--  options  -->
                        <!-- props -->
                        <!-- <el-cascader
                            ref="elcascader"
                            clearable
                            :props="rightProps"
                            v-model="PidArr"
                            :options="cityCate"
                            @change="handleChange"
                            @visible-change="elCascaderOnlick"
                            @expand-change="elCascaderOnlick"
                        ></el-cascader> -->
                        <el-select v-model="cateForm.pid" placeholder="">
                            <el-option
                                v-for="item in cityCate"
                                :key="item.id"
                                :label="item.city"
                                :value="item.id"
                            >
                            </el-option>
                        </el-select>
                    </div>
                </el-form-item>
                <!-- <el-form-item
                    label=""
                    prop="cate_gamelogo"
                    v-if="fengmian"
                >
                    <el-upload
                        :on-remove="handleRemove"
                        list-type="picture"
                        :action="uploadUrl"
                        :on-success="handleSuccess"
                        :limit="number"
                        ref="upload"
                    >
                        <el-button size="small" type="primary"
                            ></el-button
                        >
                        <div slot="tip" class="el-upload__tip">
                            jpg/png500kb
                        </div>
                    </el-upload>
                </el-form-item> -->
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="addCate"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title="/"
            :close-on-click-modal="false"
            :visible.sync="editDialogVisible"
            width="700px"
            center
            @close="editDialogClose"
        >
            <!--  -->
            <el-form
                ref="editCateFormRef"
                :model="editCateForm"
                :rules="editCateFormRules"
                label-width="100px"
            >
                <!--  -->
                <el-form-item label="ID" prop="id">
                    <el-input
                        v-model="editCateForm.id"
                        class="cate_name"
                        disabled
                    ></el-input>
                </el-form-item>
                <el-form-item label="/" prop="city">
                    <el-input
                        v-model="editCateForm.city"
                        class="cate_name"
                    ></el-input>
                </el-form-item>
                <!-- <img :src="editCateForm.cate_gamelogo" alt="" class="imglogo" /> -->
                <!-- <el-form-item
                    label=""
                    prop="cate_gamelogo"
                    v-if="fengmian"
                >
                    <el-upload
                        :on-remove="handleRemove"
                        list-type="picture"
                        :action="uploadUrl"
                        :on-success="handleSuccess"
                        :limit="number"
                        ref="upload"
                    >
                        <el-button size="small" type="primary"
                            ></el-button
                        >
                        <div slot="tip" class="el-upload__tip">
                            jpg/png500kb
                        </div>
                    </el-upload>
                </el-form-item> -->
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false"> </el-button>
                <el-button type="primary" @click="editCateName"
                    > </el-button
                >
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // 1
            number: 1,
            // 
            uploadUrl: 'http://106.14.211.218:3000/api/admin/v1/goods/upload/',
            //  
            fengmian: true,
            // 
            catelist: [],
            // table
            columns: [
                {
                    label: '',
                    prop: 'city'
                    // width: '300px'
                },
                {
                    label: '',
                    type: 'template', // 
                    template: 'order' // 
                },
                {
                    label: '',
                    type: 'template', // 
                    template: 'children_order' // 
                },
                {
                    label: '',
                    type: 'template', // 
                    template: 'operate' // 
                },
                {
                    label: '',
                    type: 'template', // 
                    template: 'city_model' // 
                }
            ],
            // 
            dialogVisible: false,
            // 
            cityCate: [],
            // props
            rightProps: {
                checkStrictly: true,
                expandTrigger: 'hover',
                value: 'id',
                label: 'cate_name',
                children: 'children'
            },
            // pid
            PidArr: [],
            value: '',
            // 
            cateForm: {
                city: '',
                pid: '',
                order: ''
            },
            // 
            cateFormRules: {
                city: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }
                ]
            },
            // 
            editDialogVisible: false,
            // 
            editCateForm: {
                city: '',
                id: ''
            },
            // 
            editCateFormRules: {
                city: [
                    {
                        required: true,
                        message: '/',
                        trigger: 'blur'
                    }
                ]
            }
        }
    },
    // 
    created() {
        this.getCateList()
    },

    // 
    methods: {
        // 
        async getCateList() {
            let { data: res } = await this.$http.get('city/getcities', {
                params: {
                    type: 2,
                    pagesize: 100,
                    pagenum: 1
                }
            })
            if (res.code === 200) {
                this.catelist = res.data
                // console.log(this.catelist)
                // this.catelist = [
                //     {
                //         city: '',
                //         children: [
                //             {
                //                 city: '1cate_'
                //             },
                //             {
                //                 city: ''
                //             }
                //         ]
                //     }
                // ]
            }
        },
        // 
        // async changeOrder(item, sta) {
        //     // 
        //     if (sta === 1) {
        //         // 1
        //         item.order++
        //         // 
        //         for (let i = 0; i < this.catelist.length; i++) {
        //             //  1
        //             if (item.order === this.catelist[i].order) {
        //                 this.catelist[i].order -= 1
        //                 // 
        //                 this.$http.put('city/order', {
        //                     id: this.catelist[i].id,
        //                     order: this.catelist[i].order
        //                 })
        //             }
        //         }
        //         //  
        //         if (item.order > this.catelist.length) {
        //             item.order = this.catelist.length
        //         }
        //     } else {
        //         item.order--
        //         for (let i = 0; i < this.catelist.length; i++) {
        //             //  1
        //             if (item.order === this.catelist[i].order) {
        //                 this.catelist[i].order += 1
        //                 // 
        //                 this.$http.put('city/order', {
        //                     id: this.catelist[i].id,
        //                     order: this.catelist[i].order
        //                 })
        //             }
        //         }
        //         if (item.order < 1) {
        //             item.order = 1
        //         }
        //     }
        //     let { data: res } = await this.$http.put('city/order', {
        //         id: item.id,
        //         order: item.order
        //     })
        //     if (res.code === 200) {
        //         this.getCateList()
        //     }
        // },
        // 
        async getCate() {
            let { data: res } = await this.$http.get('city/getcities', {
                params: {
                    pagenum: 1,
                    pagesize: 100,
                    type: 1
                }
            })
            if (res.code === 200) {
                this.cityCate = res.data
                this.dialogVisible = true
            }
        },
        selectorChange() {},
        // 
        handleChange() {
            this.cateForm.cate_gamelogo = ''
            //  pidform
            if (this.PidArr.length === 0) {
                this.cateForm.pid = 0
                this.fengmian = true
            } else {
                this.cateForm.pid = this.PidArr[this.PidArr.length - 1]
                this.fengmian = false
            }
        },
        // bug
        elCascaderOnlick() {
            let that = this
            setTimeout(function () {
                document
                    .querySelectorAll('.el-cascader-node__label')
                    .forEach((el) => {
                        el.onclick = function () {
                            this.previousElementSibling.click()
                            that.$refs.elcascader.dropDownVisible = false
                        }
                    })
                document
                    .querySelectorAll('.el-cascader-panel .el-radio')
                    .forEach((el) => {
                        el.onclick = function () {
                            that.$refs.elcascader.dropDownVisible = false
                        }
                    })
            }, 100)
        },
        // 
        addCate() {
            // pidarr  order
            if (this.cateForm.pid > 0) {
                this.cateForm.order = 0
            } else {
                this.cateForm.order = 1
            }
            // 
            this.$refs.cateFormRef.validate(async (valid) => {
                if (valid) {
                    let { data: res } = await this.$http.post(
                        'city/addcity',
                        this.cateForm
                    )
                    if (res.status === 200) {
                    }
                    this.dialogVisible = false
                    this.getCateList()
                }
            })
        },
        //  
        dialogClose() {
            this.$refs.cateFormRef.resetFields()
            this.PidArr = []
            this.cateForm = {
                city: '',
                pid: '',
                order: ''
            }
            // 
            // this.clearList()
        },
        // 
        async cateEdit(row) {
            this.editDialogVisible = true
            this.editCateForm.id = row.id
            this.editCateForm.city = row.city
        },
        // 
        editCateName() {
            this.$refs.editCateFormRef.validate(async (valid) => {
                if (valid) {
                    let { data: res } = await this.$http.put(
                        'city/editcity',
                        this.editCateForm
                    )
                    if (res.code === 200) {
                        // 
                        this.editDialogVisible = false
                        // 
                        this.getCateList()
                    }
                }
            })
        },
        // 
        editDialogClose() {
            this.$refs.editCateFormRef.resetFields()
            this.editCateForm = {
                city: '',
                id: ''
            }
            // 
            // this.clearList()
        },
        //  
        async cateDelete(item) {
            let result = await this.$confirm(
                ', ?',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }
            ).catch((err) => err)
            if (result === 'confirm') {
                let { data: res } = await this.$http.delete('city/delcity', {
                    params: { id: item.id, pid: item.pid }
                })
                if (res.code === 200) {
                    this.getCateList() //  
                }
            }
        },
        //  
        handleRemove(file) {
            this.cateForm.cate_gamelogo = ''
        },
        // 
        handleSuccess(response) {
            this.cateForm.cate_gamelogo = response.data.path
            this.editCateForm.cate_gamelogo = response.data.path
        },
        // 
        clearList() {
            this.$refs.upload.clearFiles()
        },
        // 
        async cityModelChange(e) {
            await this.$http.put('city/putmodel', {
                id: e.id,
                city_model: e.city_model
            })
        },
        // 
        async changeOrder(id, pid, order) {
            const { data: res } = await this.$http.put('city/putcitychild', {
                id: id,
                pid: pid,
                order: order
            })
            if (res.code === 200) {
                this.getCateList()
            }
        }
    }
}
</script>

<style lang="stylus">
#catelist
    i {
        font-size 12px
        margin-right 5px
    }
    .el-dialog
        .el-form
            padding-left 71px
            .cate_name
                width 400px
        .dialog-footer
            position absolute
            bottom 20px
            right 124px
.el-dialog__title
    position relative
    top -20px
    left -20px
    display block !important
    height 40px !important
    line-height 40px !important
    width 700px !important
    background rgba(217,217,217,0.5)
.el-icon-close:before
    position relative
    top -7px
.imglogo
    width 240px
    height 180px
</style>
