</template>

<script>
export default {
    // 
    props: ['userInfo'],
    data() {
        // 
        // let checkEmail = (rule, value, cb) => {
        //     const regEmail =
        //         /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
        //     if (regEmail.test(value)) {
        //         return cb()
        //     }
        //     cb(new Error(''))
        // }
        // 
        // let checkMobile = (rule, value, cb) => {
        //     const regMobile =
        //         /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/
        //     if (regMobile.test(value)) {
        //         return cb()
        //     }
        //     cb(new Error(''))
        // }
        // 
        let isName = (rule, value, cb) => {
            if (this.isname === '') {
                return cb()
            }
            cb(new Error(''))
        }
        // 
        // let isEmail = (rule, value, cb) => {
        //     if (this.isemail === '') {
        //         return cb()
        //     }
        //     cb(new Error(''))
        // }
        // 
        // let isMobile = (rule, value, cb) => {
        //     if (this.ismobile === '') {
        //         return cb()
        //     }
        //     cb(new Error(''))
        // }

        return {
            // 
            queryInfo: {
                kewords: '',
                pagenum: 1,
                pagesize: 20
            },
            userlist: [],
            total: 3,
            flag: true, // / /
            addDialogVisible: false, // /
            editDialogVisible: false, // 
            chongDialogVisible: false,
            isname: '', // rule
            isemail: '',
            ismobile: '',
            // 
            addFormRules: {
                username: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 2,
                        max: 16,
                        message: ' 2  16 ',
                        trigger: 'blur'
                    },
                    {
                        validator: isName,
                        trigger: 'blur'
                    }
                ],
                password: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 8,
                        max: 16,
                        message: ' 8  16 ',
                        trigger: 'blur'
                    }
                ],
                chong: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 1,
                        max: 8,
                        message: ' 1  8',
                        trigger: 'blur'
                    }
                ]
                // gaipassword: [
                //     {
                //         min: 8,
                //         max: 20,
                //         message: ' 8  20 ',
                //         trigger: 'blur'
                //     }
                // ],
                // mobile: [
                //     {
                //         required: true,
                //         message: '',
                //         trigger: 'blur'
                //     },
                //     {
                //         validator: checkMobile,
                //         trigger: 'blur'
                //     },
                //     {
                //         validator: isMobile,
                //         trigger: 'blur'
                //     }
                // ],
                // email: [
                //     {
                //         required: true,
                //         message: '',
                //         trigger: 'blur'
                //     },
                //     {
                //         validator: checkEmail,
                //         trigger: 'blur'
                //     },
                //     {
                //         validator: isEmail,
                //         trigger: 'blur'
                //     }
                // ]
            },
            // 
            addForm: {
                username: '',
                password: ''
            },
            // 
            editForm: {
                username: '',
                password: '',
                id: '',
                chong: ''
            },
            tableWidth: '',
            remark_id: ''
        }
    },
    created() {
        this.getUserList()
    },
    methods: {
        // 
        async getUserList() {
            let { data: res } = await this.$http.get('user/userlistfalse', {
                params: this.queryInfo
            })
            //  div
            if (res.code !== 200) {
                this.total = 0
            } else {
                this.userlist = res.data
                this.total = res.total
            }
        },
        // pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getUserList()
        },
        // pagenum
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getUserList()
        },
        // 
        async userStateChange(row) {
            await this.$http.put('user/updatestate', {
                id: row.id,
                status: row.status
            })
            this.getUserList() //  
        },
        // 
        async userChong() {
            const { data: res } = await this.$http.put(
                'user/userchong',
                this.editForm
            )
            if (res.code === 200) {
                this.chongDialogVisible = false // 
                this.getUserList() //  
                this.$message.success({
                    message: '',
                    offset: 201,
                    center: true
                })
            } else {
                this.$message.error({
                    message: '',
                    offset: 201,
                    center: true
                })
            }
        },
        //  
        searchUser() {
            this.queryInfo.pagenum = 1
            this.getUserList()
        },
        // 
        researchUser() {
            this.queryInfo.pagenum = 1
            this.getUserList()
        },
        //   ref="ruleFormRef"
        dialogClose() {
            this.$refs.ruleFormRef.resetFields()
        },
        //  
        addUser() {
            this.$refs.ruleFormRef.validate(async (valid) => {
                if (valid) {
                    // this.addDialogVisible = false
                    let { data: res } = await this.$http.post(
                        'user/adduser',
                        this.addForm
                    )
                    if (res.code === 200) {
                        this.getUserList() //  
                        this.$message.success({
                            message: '',
                            offset: 200,
                            center: true
                        })
                        // 
                        this.addDialogVisible = false
                    } else {
                        this.$message.error({
                            message: '',
                            offset: 200,
                            center: true
                        })
                    }
                }
            })
        },
        //     ( formname)
        checkName(formname) {
            // isname
            this.isname = ''
            // api
            this.$refs.ruleFormRef.validateField(
                'username',
                async (errorMessage) => {
                    if (!errorMessage) {
                        let { data: res } = await this.$http.get(
                            'user/checkname',
                            { params: { username: formname.username } }
                        )

                        if (res === 1) {
                            // isname
                            this.isname = 'exist'
                            // 
                            this.$refs.ruleFormRef.validateField('username')
                        } else {
                            this.isname = ''
                            // 
                            this.$refs.ruleFormRef.validateField('username')
                        }
                    }
                }
            )
        },
        // 
        checkMobile(formname) {
            // ismobile
            this.ismobile = ''
            // api
            this.$refs.ruleFormRef.validateField(
                'mobile',
                async (errorMessage) => {
                    if (!errorMessage) {
                        let { data: res } = await this.$http.get(
                            'users/addcheck',
                            {
                                params: {
                                    mobile: formname.mobile,
                                    id: formname.id
                                }
                            }
                        )
                        if (res === 1) {
                            // ismobile
                            this.ismobile = 'exist'
                            // 
                            this.$refs.ruleFormRef.validateField('mobile')
                        }
                    }
                }
            )
        },
        // 
        checkEmail(formname) {
            // isemail
            this.isemail = ''
            // api
            this.$refs.ruleFormRef.validateField(
                'email',
                async (errorMessage) => {
                    if (!errorMessage) {
                        let { data: res } = await this.$http.get(
                            'users/addcheck',
                            {
                                params: {
                                    email: formname.email,
                                    id: formname.id
                                }
                            }
                        )
                        if (res === 1) {
                            // isemail
                            this.isemail = 'exist'
                            // 
                            this.$refs.ruleFormRef.validateField('email')
                        }
                    }
                }
            )
        },
        // 
        async showEditDialog(row) {
            this.editDialogVisible = true
            this.editForm = row
            this.editForm.password = ''
        },
        // 
        updateUser() {
            this.$refs.ruleFormRef.validate(async (valid) => {
                if (valid) {
                    let { data: res } = await this.$http.put(
                        'user/updateuser',
                        this.editForm
                    )
                    if (res.code === 200) {
                        this.editDialogVisible = false // 
                        this.getUserList() //  
                        this.$message.success({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    } else {
                        this.$message.error({
                            message: '',
                            offset: 201,
                            center: true
                        })
                    }
                }
            })
        },
        // 
        async removeUserById(id) {
            let result = await this.$confirm(
                ', ?',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    closeOnClickModal: false
                }
            ).catch((err) => err)
            if (result === 'confirm') {
                let { data: res } = await this.$http.delete('user/deleteuser', {
                    params: { id: id }
                })
                if (res.code === 200) {
                    this.getUserList() //  
                    this.$message.success({
                        message: '',
                        offset: 201,
                        center: true
                    })
                }
            }
        },
        // 
        showChongDialog(row) {
            this.chongDialogVisible = true
            this.editForm.id = row.id
            this.editForm.username = row.username
        },
        // /
        async editRemarks(row) {
            const { data: res } = await this.$http.put('user/upremarks', {
                id: row.id,
                admin_remarks: row.admin_remarks
            })
            if (res.code === 200) {
                this.remark_id = ''
                this.tableWidth = ''
            } else {
                this.$message.error({
                    message: ', ',
                    offset: 201,
                    center: true
                })
            }
        },
        // 
        remarkClick(row) {
            this.remark_id = row.id
            this.tableWidth = 400
            this.$nextTick(() => {
                this.$refs.remarkInput.focus()
            })
        },
        // 
        async userPass(id) {
            let { data: res } = await this.$http.put('user/userpass', {
                id: id,
                status: true
            })
            if (res.code === 200) {
                this.getUserList() //  
                // this.$message.success({
                //     message: '',
                //     offset: 201,
                //     center: true
                // })
            }
        }
    }
}
</script>

